      .bloque-texto-g__texto.p-4
        p.mb-0 La era de la informtica introdujo un nuevo elemento en las empresas, las universidades y una multitud de otras organizaciones: un conjunto de componentes denominado sistema de informacin, que se ocupan de recopilar y organizar datos e informacin.
          br
          br
          | Un sistema de informacin es una combinacin de #[i hardware] y #[i software] y redes de telecomunicaciones que las personas crean para recopilar, crear y distribuir datos tiles, por lo general en una organizacin, adems permite definir el flujo de informacin.
    .row.justify-content-center.mb-4(data-aos="fade-down")
      .col-lg-auto.bg-c21.shadow
        .p-2
          p.text-center.text-white.mb-0 #[b Los componentes del sistema de informacin son los siguientes:]
    .tarjeta.tarjeta--azul2(data-aos="fade-left")
      .p-4
        SlyderA(tipo="b")
          .row.align-items-center.justify-content-center
            .col-lg-6.mb-4.mb-lg-0
              p #[b #[i Hardware] de la computadora]
              p.mb-0 Esta es la tecnologa fsica que trabaja con la informacin. El #[i hardware] puede ser tan pequeo como un telfono inteligente que cabe en un bolsillo o tan grande como una supercomputadora que llena un edificio. El #[i hardware] tambin incluye los dispositivos perifricos que funcionan con computadoras, como teclados, unidades de disco externas y enrutadores. Con el auge del Internet de las cosas, en el que cualquier cosa, desde electrodomsticos hasta automviles y ropa, podr recibir y transmitir datos, los sensores que interactan con las computadoras estn impregnando el entorno humano. Por tanto, es aquel equipo fsico utilizado para la entrada, salida y procesamiento. La estructura del #[i hardware] depende del tipo y tamao de la organizacin. Consiste en un dispositivo de entrada y salida, sistema operativo, procesador y dispositivos de medios. Esto tambin incluye dispositivos perifricos de computadora.
            .col-lg-5
              figure
                img(src='@/assets/curso/tema2/img2.png', style="width: 470px", alt='Imgen decorativa').m-auto
          .row.align-items-center.justify-content-center
            .col-lg-6.mb-4.mb-lg-0
              p #[b #[i Software] de ordenador]
              p.mb-0 El #[i hardware] necesita saber qu hacer, y ese es el papel del #[i software]. El #[i software] se puede dividir en dos tipos: #[i software] de sistema y #[i software] de aplicacin. La pieza principal del #[i software] del sistema es el sistema operativo, como Windows o iOS, que administra el funcionamiento del #[i hardware]. El #[i software] de aplicacin est diseado para tareas especficas, como manejar una hoja de clculo, crear un documento o disear una pgina web. Los programas de aplicacin utilizados para controlar y coordinar los componentes de #[i hardware]. Se utiliza para el anlisis y procesamiento de los datos. Estos programas incluyen un conjunto de instrucciones utilizadas para procesar informacin.
            .col-lg-5
              figure
                img(src='@/assets/curso/tema2/img3.png', style="width: 470px", alt='Imgen decorativa').m-auto
          .row.align-items-center.justify-content-center
            .col-lg-6.mb-4.mb-lg-0
              p #[b Telecomunicaciones]
              p.mb-0 Este componente conecta el #[i hardware] para formar una red. Las conexiones pueden ser a travs de cables, como Ethernet o fibra ptica, o inalmbricas, como a travs de Wi-Fi. Se puede disear una red para unir computadoras en un rea especfica, como una oficina o una escuela, a travs de una red de rea local (LAN). Si las computadoras estn ms dispersas, la red se denomina red de rea amplia (WAN). Internet puede considerarse una red de redes.
            .col-lg-5
              figure
                img(src='@/assets/curso/tema2/img4.png', style="width: 470px", alt='Imgen decorativa').m-auto
          .row.align-items-center.justify-content-center
            .col-lg-6.mb-4.mb-lg-0
              p #[b Bases de datos]
              p Los datos son los hechos y cifras sin procesar que no estn organizados y que luego se procesan para generar informacin. Los #[i software] se utilizan para organizar y entregar datos al usuario, administrar el almacenamiento fsico de medios y recursos virtuales. Como el #[i hardware] no puede funcionar sin el #[i software], al igual que el #[i software] necesita datos para su procesamiento. Los datos se gestionan mediante el sistema de gestin de base de datos.
              p.mb-0 Una base de datos es un lugar donde se recopilan datos y desde el cual se pueden consultar utilizando algunos criterios especficos. Las bases de datos han adquirido una importancia en los sistemas de informacin con la aparicin de "big data", un trmino que se refiere a las cantidades realmente masivas de datos que se pueden recopilar y analizar.
            .col-lg-5
              figure
                img(src='@/assets/curso/tema2/img5.png', style="width: 470px", alt='Imgen decorativa').m-auto
          .row.align-items-center.justify-content-center
            .col-lg-6.mb-4.mb-lg-0
              p #[b La red]
              p.mb-0 Los recursos de redes se refieren a las redes de telecomunicaciones como la intranet, extranet e internet, estos recursos facilitan el flujo de informacin en la organizacin; las redes abarcan dispositivos fsicos, como tarjetas de red, enrutadores, concentradores y cables, y #[i software], como sistemas operativos, servidores web, servidores de datos y servidores de aplicaciones. Las redes de telecomunicaciones estn conformadas por computadoras, procesadores de comunicaciones y otros dispositivos interconectados por medios de comunicacin y controlados por #[i software].
            .col-lg-5
              figure
                img(src='@/assets/curso/tema2/img6.png', style="width: 470px", alt='Imgen decorativa').m-auto
          .row.align-items-center.justify-content-center
            .col-lg-6.mb-4.mb-lg-0
              p #[b Recursos humanos y procedimientos]
              p.mb-0 Est asociado con la mano de obra requerida para operar y administrar el sistema. Las personas son el usuario final del sistema de informacin, el usuario final utiliza la informacin producida para sus propios fines, el objetivo principal del sistema de informacin es beneficiar a las personas. Los usuarios finales pueden ser contadores, ingenieros, vendedores, clientes, empleados o gerentes, etc. Las personas tambin son responsables de desarrollar y operar los sistemas de informacin. Incluyen analistas de sistemas, operadores de computadoras, programadores y otro personal administrativo de S y tcnicas gerenciales.
            .col-lg-5
              figure
                img(src='@/assets/curso/tema2/img7.png', style="width: 470px", alt='Imgen decorativa').m-auto
