                  .row.m-1
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Qu hace un sistema operativo?]
                      p El sistema operativo es el #[i software] que controla todos los recursos de un sistema informtico. Por ejemplo:
                      ul.lista-ul--color.mb-0
                        li.d-flex
