                        img(src='@/assets/curso/tema2/img10.png', style="width: 450px", alt='Imgen decorativa').m-auto
                  .row.m-1
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Qu es la multiprogramacin?]
                      p Es la capacidad de una computadora para ejecutar varios programas simultneamente en el mismo procesador al tener uno de los programas usando el procesador mientras otros realizan la entrada o salida. La multiprogramacin requiere que las partes activas de los programas que compiten por el procesador estn disponibles en la memoria principal. La memoria virtual es la capacidad aparentemente expandida de la memoria principal de la computadora, y se logra al mantener en la memoria principal solo las partes de los programas que se necesitan de inmediato, con los programas completos guardados en el almacenamiento secundario.
                    .col-lg-4
                      figure
                        img(src='@/assets/curso/tema2/img11.png', style="width: 450px", alt='Imgen decorativa').m-auto
                  .row.m-1
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Modos de operacin del sistema informtico]
                      p Los sistemas operativos permiten que el sistema que administran opere en varios modos. Estos incluyen el procesamiento por lotes, el tiempo compartido y el procesamiento en tiempo real.
                      ul.lista-ul--color
