                          |
                          p.mb-0 Sistemas de tiempo compartido que brindan procesamiento interactivo al asignar un segmento de tiempo corto para el uso del servidor a cada usuario por turno.
                        li.d-flex
                          i.fas.fa-check-circle(style="color: #4ADBD1")
                          |
                          p.mb-0 Sistemas de procesamiento en tiempo real que responden a un evento dentro de un intervalo de tiempo fijo; se utiliza, por ejemplo, en plantas de fabricacin o para recopilar datos de varios equipos en un laboratorio.
                      p.mb-0 Los sistemas operativos de multiprocesamiento se han diseado para asignar el trabajo a los mltiples procesadores. Estos sistemas tambin admiten la multiprogramacin, lo que permite que muchos programas compitan por los procesadores.
                    .col-lg-4
