                  .row.m-1
                    .col-lg-8.mb-4.mb-lg-0
                      p.txt-c1.fs-5 #[b Sistemas operativos computadoras personales]
                      p Los sistemas operativos de las computadoras personales que estn dedicadas a un solo usuario son mucho ms simples que los sistemas operativos que ejecutan mquinas ms grandes, a las que cientos o miles de usuarios pueden tener acceso simultneo. Una capacidad importante que puede ofrecer un sistema operativo en un entorno informtico personal es la multitarea: la capacidad de ejecutar varias tareas a la vez en nombre de un usuario.
