                img(src='@/assets/curso/tema2/img19.png', style="width: 350px", alt='Imgen decorativa').m-auto
            .col-lg-6
              p #[b Gestin de base de datos]
              p Facilitan el almacenamiento, el mantenimiento y la utilizacin de datos en una base de datos compartida por muchos usuarios. Los DBM de microcomputadoras permiten a los usuarios:
              ul.lista-ul--color
                li.d-flex
                  i.fas.fa-check-circle(style="color: #4ADBD1")
                  |
                  p.mb-0 Crear y mantener una base de datos.
                li.d-flex
                  i.fas.fa-check-circle(style="color: #4ADBD1")
                  |
                  p.mb-0 Consultar una base de datos con un lenguaje de consulta.
                li.d-flex
                  i.fas.fa-check-circle(style="color: #4ADBD1")
                  |
                  p.mb-0 Preparar informes formateados.
              p.mb-0 Adems, los paquetes ofrecen funciones de seguridad, conectividad de red y la capacidad de presentar resultados grficos, as como realizar clculos tipo hoja de clculo.
          .row.align-items-center.justify-content-center
            .col-lg-3.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema2/img20.png', style="width: 350px", alt='Imgen decorativa').m-auto
            .col-lg-6
              p #[b Procesamiento de textos]
              p Son programas que informatizan la creacin, edicin e impresin de documentos mediante el procesamiento electrnico de datos de texto. El procesamiento de textos es una aplicacin importante de la automatizacin de oficinas. El procesamiento de textos es la actividad de creacin y presentacin ms popular. De hecho, es la aplicacin informtica personal ms comn.
          .row.align-items-center.justify-content-center
            .col-lg-3.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema2/img21.png', style="width: 350px", alt='Imgen decorativa').m-auto
            .col-lg-6
              p #[b Autoedicin]
              p Las organizaciones utilizan #[i software] de autoedicin para producir sus propios materiales impresos, como boletines, folletos, manuales y libros con varios estilos de letra, grficos y colores en cada pgina. Los componentes necesarios para configurar un sistema de autoedicin modesto incluyen: una pantalla de alta resolucin, una impresora lser, #[i software] de autoedicin y quizs un escner.
          .row.align-items-center.justify-content-center
            .col-lg-3.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema2/img22.png', style="width: 350px", alt='Imgen decorativa').m-auto
            .col-lg-6
              p #[b #[i Software] de presentacin]
              p El objetivo de los grficos de presentacin es proporcionar informacin en forma grfica que ayude a los usuarios finales y gerentes a comprender las propuestas comerciales y el rendimiento y tomar mejores decisiones al respecto. Es importante tener en cuenta que los grficos de presentacin no reemplazan los informes y las pantallas de nmeros y material de texto.
          .row.align-items-center.justify-content-center
            .col-lg-3.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema2/img23.png', style="width: 350px", alt='Imgen decorativa').m-auto
            .col-lg-6
              p #[b #[i Software] de autora multimedia]
              p Permite a sus usuarios disear presentaciones multimedia. Con estos paquetes, puede desarrollar cursos atractivos de capacitacin basada en computadora (CBT) o presentaciones para clientes. Este #[i software] tiene imgenes grficas superiores, animacin por computadora y video en movimiento que se pueden combinar con sonido de alta fidelidad en combinacin con el texto.
          .row.align-items-center.justify-content-center
            .col-lg-3.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema2/img24.png', style="width: 350px", alt='Imgen decorativa').m-auto
            .col-lg-6
              p #[b Los hipermedios]
              p Son documentos electrnicos que contienen mltiples formas de medios, incluidos texto, grficos, video, mensajes de voz u otras unidades de informacin, como hojas de trabajo. En el mtodo hipermedia de entrega de informacin, se pueden establecer vnculos entre varios elementos en un gran documento multimedia. Estos enlaces permiten al usuario pasar de un tema directamente a otro relacionado, en lugar de escanear la informacin de manera secuencial. El hipertexto es una metodologa para la construccin y uso interactivo de bases de datos de texto. Por definicin, el hipertexto contiene solo texto y una cantidad limitada de grficos.
