        p.mb-0 Los paquetes de gestin de informacin personal (PIM) son herramientas que ayudan a los trabajadores del conocimiento a realizar un seguimiento de tareas, personas, proyectos, compromisos e ideas. Estos paquetes ayudan a los usuarios finales a almacenar, organizar y recuperar texto y datos numricos en forma de notas, listas, recortes, tablas, memorandos, cartas, informes, etc.
