                .crd_hover_txt--body
                  p.text-center #[b Fuente interna]
                  p.text-center.mb-0 Es el flujo de informacin que generan las entidades internas de la organizacin (suministros, produccin, gestin de empleados, contabilidad, ventas, etc.), por su funcionamiento (procesos, mtodos), pero tambin el flujo de informacin informal (clima social, bienestar de los empleados, saber hacer, etc.) menos sencilla de destacar, pero decisiva, etc.
            .col-xl-5.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0
