                .crd_hover_txt--body1
                  p.text-center.text-white #[b Fuente externa]
                  p.text-center.text-white.mb-0 Este es el flujo de informacin generado por partes interesadas externas a la empresa (clientes, proveedores, Estado, etc.) y que son esenciales para anticipar cambios y adaptar los SI para servir a la organizacin. Al establecer un monitoreo (tecnolgico, social, legal, comercial, etc.), la empresa se da cuenta de que es fundamental para ella estar particularmente atenta a la informacin de fuentes externas.
    .row.mb-4(data-aos="fade-right")
