        p.mb-0 Una vez recolectada e ingresada, la informacin debe almacenarse de manera duradera, estable y segura para que pueda ser utilizada posteriormente o simplemente para cumplir con obligaciones legales. Para organizar el almacenamiento de la informacin, se implementan medios tcnicos y organizativos tales como mtodos de archivo, tcnicas de respaldo, proteccin contra la piratera o mtodos para evitar la destruccin de datos. Como la informacin es valiosa, es imperativo que SI la asegure bien.
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Organizacin de almacenamiento
    .row.align-items-center.mb-5
      .col-lg-3.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema2/img29.png', style="width: 350px", alt='Imgen decorativa').m-auto
      .col-lg-9(data-aos="fade-left")
        .cajon.color-primario.p-4
          p.mb-0 Por lo tanto, la informacin se recopila y almacena en archivos o en lo que se denomina una base de datos (o BDD). El archivo es una coleccin, un conjunto de datos reunidos bajo el mismo nombre. Tcnicamente, es informacin digital compuesta por una secuencia de bytes, es decir, una secuencia de nmeros. La base de datos (BDD) es una estructura de almacenamiento de informacin que toma la forma de grandes tablas, como una tabla de Excel. Cada columna se utiliza para ordenar un tipo de datos y cada fila representa un registro y, por ende, puede encontrar en la columna el nmero del contacto, su nombre, su nombre, su funcin y su nmero de telfono. Cada lnea representa un registro.
    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Zona de almacenamiento
    p.mb-5(data-aos="fade-right") Finalmente, todos los archivos y bases de datos deben almacenarse. Se pueden encontrar las siguientes zonas de almacenamiento:
    .row.bg-c26.mb-5(data-aos="fade-down")
      .col-12
        .px-5.pb-5.pt-1
          figure
            img(src='@/assets/curso/tema2/img11.svg', style="width: 1150px", alt='Directamente en los discos duros de los servidores SI de la organizacin. En este contexto, la informacin solo es accesible desde este servidor. En la Nube. En este contexto, la informacin es accesible desde cualquier parte del mundo.	En reas de almacenamiento dentro del SI de la organizacin. Estos son discos duros muy grandes accesibles a travs de la red corporativa. En este contexto, la informacin es accesible desde todos los componentes del SI, pero slo dentro de la organizacin.').m-auto

    .row.mb-4(data-aos="fade-right")
      .col-auto.px-4.py-3.bg-c3
        h3.mb-0 Para procesar informacin
    .row.align-items-center.mb-5
