        figure
          img(src='@/assets/curso/tema1/img1.svg', style="width: 30px", alt='Imgen decorativa').m-auto
      .col(data-aos="fade-left")
        p.mb-0 Una vez que la informacin ha sido recopilada y almacenada, est disponible para su procesamiento, este proceso consiste en producir nueva informacin a partir de la informacin existente a travs de programas de computadora u operaciones manuales.
    .row.bg-c12.mb-5(data-aos="fade-left")
      .col-12
        .p-5
