            .col-lg-auto.bg-c29.shadow
              .p-2
                p.text-center.text-white.mb-0 #[b El procesamiento de la informacin puede tomar 4 formas diferentes, obsrvelas a continuacin:]
          .row.justify-content-center
            .col-lg-11
              PasosA.color-acento-contenido.mb-5(tipo="l")
                .row.align-items-center
