                    p #[b Consultar la informacin]
                    p Este es el procesamiento ms sencillo ya que consiste en acceder a la informacin tal como fue registrada.
                  .col-lg-auto
                    figure
                      img(src='@/assets/curso/tema2/img12.svg', style="width: 270px", alt='Imgen decorativa').m-auto
                .row.align-items-center
                  .col-lg-auto.mb-4.mb-lg-0
                    figure
                      img(src='@/assets/curso/tema2/img13.svg', style="width: 270px", alt='Imgen decorativa').m-auto
                  .col
                    p.txt-c2 #[b Organizar la informacin]
                    p.txt-c2 Este procesamiento consiste en estructurar la informacin segn criterios especficos. Esta podr, por ejemplo, agrupar informacin por cliente, por zona geogrfica, por actividad y muchas ms.
                .row.align-items-center
                  .col.mb-4.mb-lg-0
                    p #[b Actualizacin de la informacin]
                    p Este tratamiento consistir en tomar informacin previamente registrada y actualizarla, segn los requerimientos o necesidades organizacionales.
                  .col-lg-auto
                    figure
                      img(src='@/assets/curso/tema2/img14.svg', style="width: 270px", alt='Imgen decorativa').m-auto
                .row.align-items-center
                  .col-lg-auto.mb-4.mb-lg-0
                    figure
                      img(src='@/assets/curso/tema2/img15.svg', style="width: 270px", alt='Imgen decorativa').m-auto
                  .col
                    p.txt-c2 #[b Producir nueva informacin]
                    p.txt-c2 A partir de informacin(es) existente(s), este procesamiento permitir la creacin de nueva informacin, dicha informacin toma como base informacin ya analizada, pero se construye con el fin de suplir las necesidades organizacionales, se encamina hacia la bsqueda de diferentes soluciones, cuestionamientos, e intereses propios de la empresa.
    .row.mb-4(data-aos="fade-right")
