    p.mb-5(data-aos="fade-right") La infraestructura de TI consta de varios subsistemas que cumplen objetivos especficos, como la gestin de datos, la gestin de redes o el almacenamiento. Los subsistemas de TI trabajan juntos como parte del sistema de TI general. Es til pensar en trminos de subsistemas porque TI abarca una variedad de tecnologas. Especificar el subsistema ayuda a definir el contexto. Algunos ejemplos de subsistemas de TI son los siguientes:
